@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Russo One',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1c1e22;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.header {
    border-bottom: 0.5px solid #FFE81F;
    padding-top: 20px;
    width: 95%;
    display: flex;
    margin: 0 auto;
    padding-bottom: 10px;
}

.header__logo {
    width: 150px;
    min-width: 150px;
    height: 60px;
    background-image: url(/starships-comparison/static/media/Star_Wars_Yellow_Logo.af84c44c.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.header__title {
    margin: 20px 20px 0 30px;
    font-size: 40px;
    color: #FFE300;
    font-family: 'Russo One';
    font-weight: 400;
    box-sizing: border-box;
}

@media all and (max-width: 750px) {
    .header__logo {
        width: 80px;
        min-width: 80px;
        height: 40px;
    }

    .header__title {
        margin: 20px 10px 0 10px;
        font-size: 20px;
    }
  }

  @media all and (max-width: 400px) {
    .header__logo {
        width: 60px;
        min-width: 60px;
        height: 40px;
    }

    .header__title {
        margin: 15px 10px 0 15px;
        font-size: 18px;
    }

  }
.comparison {
    display: flex;
    flex-direction: column;
    width: 75%;
    margin: 20px auto;
    /* background-color: #474B4e; */
    background-color: #2F353B;
    border-radius: 12px;
    
}

.comparison__wrapper {
    /* width: 95%; */
    display: flex;
}

.comparison__item {
    text-align: center;
    color: white;
    width: 50%;
    font-weight: 400;
    font-size: 15px;
    padding-top: 10px;
}

.comparison__title {
    text-align: center;
    color: white;
    font-weight: 400;
    font-size: 20px;
    border-bottom: 0.5px solid gray;
    padding-bottom: 10px;
}

.comparison__name {
    text-align: center;
    color: white;
    width: 50%;
    font-weight: 400;
    font-size: 24px;
    padding-top: 20px;
    padding-bottom: 10px;
}

.comparison__image {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    width: 50%;
    padding-bottom: calc(30% - 15px);
    box-sizing: content-box;
    margin: 15px;
}

.color-green {
    color: #54ad7a;
}

.color-red {
    color: #E32636;
}

.color-yellow {
    color: #ffdb4d;
}

@media all and (max-width: 950px) {
    .comparison {
        width: 90%  
    }
}

@media all and (max-width: 660px) {
    .comparison__title {
        font-size: 16px;
        padding-top: 8px;
    }

    .comparison__image {
        padding-bottom: calc(30% - 5px);
        margin: 5px;
    }

    .comparison__name {
        font-size: 18px;
    }

    .comparison__item {
        padding-top: 0;
    }

  }
.form__input {
    font-size: 1.1em;
    max-width: 200px;
    min-width: 200px;
    height: 50px;
    border-bottom: 1px solid #f1f1f1;
    padding: 0 10px;
    box-sizing: border-box;
  }

.form__btn {
    font-size: 1.2em;
    width: 200px;
    height: 50px;
    background-color: #ffdb4d;
    border-radius: 2px;
    cursor: pointer;
    border: none;
    padding: 0;
}

.container {
    display: flex;
    flex-direction: column;
    margin: 50px auto;
    max-width: 650px;
    padding: 0 10px 0 10px;
}

.form {
    display: flex;
    justify-content: space-between;
    width: 100%;
    flex-wrap: wrap;
}

.title {
    color: white;
    font-family: 'Russo One';
    font-weight: 400;
}

@media all and (max-width: 660px) {
    .form {
        display: flex;
        width: 100%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .form__input {
        font-size: 1em;
        margin-bottom: 10px;
    }

    .title {
        font-size: 20px;
        text-align: center;
    }

  }
